var scene = new THREE.Scene();
// scene.background = new THREE.Color(0xf0f0f0);
var backgroundMesh, backgroundScene, backgroundCamera;
var camera = new THREE.PerspectiveCamera(
  70,
  window.innerWidth / window.innerHeight,
  0.1,
  12000
);
camera.position.z = -5000;
camera.position.y = window.innerWidth * 2;
camera.position.x = -window.innerWidth / 2;

var renderer = new THREE.WebGLRenderer();
renderer.setSize(window.innerWidth, window.innerHeight);
renderer.autoClear = false;
document.body.appendChild(renderer.domElement);

var controls = new THREE.OrbitControls(camera, renderer.domElement);
controls.enableDamping = true;
controls.dampingFactor = 0.25;
controls.enableZoom = true;

var keyLight = new THREE.DirectionalLight(
  new THREE.Color("hsl(30, 100%, 75%)"),
  1.0
);
keyLight.position.set(-5000, 5000, 0);

var fillLight = new THREE.DirectionalLight(
  new THREE.Color("hsl(240, 100%, 75%)"),
  0.75
);
fillLight.position.set(2500, 0, -2500);

var backLight = new THREE.DirectionalLight(0xffffff, 1.0);
backLight.position.set(0, -500, 0).normalize();

scene.add(keyLight);
scene.add(fillLight);
scene.add(backLight);

var manager = new THREE.LoadingManager();
manager.onLoad = init;

var loader = new THREE.TextureLoader(manager);

loader.load("/statics/space-background.jpg", function(texture) {
  backgroundMesh = new THREE.Mesh(
    new THREE.PlaneGeometry(2, 2, 0),
    new THREE.MeshBasicMaterial({
      map: texture
    })
  );

  backgroundMesh.material.depthTest = false;
  backgroundMesh.material.depthWrite = false;

  backgroundScene = new THREE.Scene();
  backgroundCamera = new THREE.Camera();
  backgroundScene.add(backgroundCamera);
  backgroundScene.add(backgroundMesh);
});

var mtlLoader = new THREE.MTLLoader();
mtlLoader.setTexturePath("/js/");
mtlLoader.setPath("/js/");
mtlLoader.load("starter1.mtl", function(materials) {
  materials.preload();

  var objLoader = new THREE.OBJLoader();
  objLoader.setMaterials(materials);
  objLoader.setPath("/js/");
  objLoader.load("starter1.obj", function(object) {
    scene.add(object);
    object.position.y -= 60;
  });
});

var animate = function() {
  requestAnimationFrame(animate);
  controls.update();
  renderer.clear();
  renderer.render(backgroundScene, backgroundCamera);
  renderer.render(scene, camera);
};

function init() {
  animate();
}
